#data-container {
  display: none;
}

.paginationjs {
  display: flex;
  justify-content: center;
  align-items: center;

  line-height: 1.6;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  box-sizing: initial;
}

.paginationjs:after {
  display: table;
  content: ' ';
  clear: both;
}

.paginationjs .paginationjs-pages {
  float: left;
}

.paginationjs .paginationjs-pages ul {
  @media screen and (max-width: 767px) {
    margin: 40px 0px;
  }

  float: left;
  margin: 60px 0px;
  padding: 0;
}

.paginationjs .paginationjs-go-button,
.paginationjs .paginationjs-go-input,
.paginationjs .paginationjs-nav {
  float: left;
  margin-left: 10px;
  font-size: 12px;
}

.paginationjs .paginationjs-pages li {
  float: left;
  // border: 1px solid #aaa;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  border-right: none;
  list-style: none;
  margin: 0px 8px;

  &:first-child {
    @media screen and (max-width: 767px) {
      margin-left: 0px;
      margin-right: 12px;
    }
    margin-left: 0px;
    margin-right: 8px;
  }

  &:last-child {
    @media screen and (max-width: 767px) {
      margin-left: 12px;
      margin-right: 0px;
    }
    margin-left: 8px;
    margin-right: 0px;
  }

  &.paginationjs-ellipsis {

    @media screen and (max-width: 767px) {
      display: none;
    }
  }
}

.paginationjs .paginationjs-pages li > a {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  height: 40px;
  line-height: 1.3;
  font-size: 12px;
  color: #000000;
  text-decoration: none;
}

.paginationjs .paginationjs-pages li > a:not(:last-child) {
  min-width: 40px;
  border-radius: 5px;
  background-color: #ff6b08;
  color: #ffffff;
}

.paginationjs .paginationjs-pages li.active {
  border-radius: 5px;
  background-color: #ff6b08;
  color: #ffffff;
}

.paginationjs .paginationjs-pages li.active > a {
  min-width: 40px;
  height: 40px;
  line-height: 1.3;
  background: #ff6b08;
  color: #fff;
}

.paginationjs .paginationjs-pages li.disabled > a {
  opacity: 1;
}

.paginationjs-prev,
.J-paginationjs-previous {
  min-width: 40px;
  height: 40px;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.6667 8H3.33334' stroke='black' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8.00001 12.6667L3.33334 8.00004L8.00001 3.33337' stroke='black' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #f7f7f7;

  &:hover {
    background-color: #ff6b08;
    cursor: pointer;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.6667 8H3.33334' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8.00001 12.6667L3.33334 8.00004L8.00001 3.33337' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: center;
  }

  &.disabled {
    cursor: auto;
  }
}

.paginationjs-prev:hover,
.J-paginationjs-previous:hover {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.6667 8H3.33334' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8.00001 12.6667L3.33334 8.00004L8.00001 3.33337' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
}

.paginationjs .paginationjs-pages li:first-child,
.paginationjs .paginationjs-pages li:first-child > a {
  border-radius: 5px;
}

.paginationjs .paginationjs-pages li:last-child {
  border-right: 1px solid #aaa;
  border-radius: 5px;
}

.paginationjs .paginationjs-pages li:last-child > a {
  border-radius: 5px;
}

.paginationjs .paginationjs-go-input > input[type='text'] {
  width: 30px;
  height: 28px;
  background: #fff;
  border-radius: 5px;
  border: 1px solid #aaa;
  padding: 0;
  font-size: 14px;
  text-align: center;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  box-sizing: initial;
}

.paginationjs .paginationjs-go-button > input[type='button'] {
  min-width: 40px;
  height: 30px;
  line-height: 28px;
  background: #fff;
  border-radius: 5px;
  // border: 1px solid #aaa;
  text-align: center;
  padding: 0 8px;
  font-size: 14px;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  color: #333;
  cursor: pointer;
  vertical-align: middle\9;
}

