.paginationjs .paginationjs-pages li.paginationjs-next a {
    display: flex;
    align-items: center;
    justify-content: center;
  min-width: 40px;
  height: 40px;
  border-radius: 5px;
  border-right: none;

  // transform: matrix(-1, 0, 0, 1, 0, 0);
  // transform: scaleX(-1);

  &:after {
   
    content: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.6667 8H3.33334' stroke='black' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8.00001 12.6667L3.33334 8.00004L8.00001 3.33337' stroke='black' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    border-radius: 5px;
    background-color: #f7f7f7;
    width: 100%;
    height: 100%;
    transform: rotate(180deg);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    z-index: -1;
  }

  &:hover {
   
    &::after {
         
        content: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.6667 8H3.33334' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8.00001 12.6667L3.33334 8.00004L8.00001 3.33337' stroke='white' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        background-color: #ff6b08;
        z-index: -1;
    }
  }
}

